@font-face {
  font-family: 'Montserrat';
  font-weight: 400;
  font-style: normal;
  src: url('../assets/fonts/Montserrat-Regular.ttf');
}

@font-face {
  font-family: 'Montserrat Semi-Bold';
  font-weight: 600;
  font-style: normal;
  src: url('../assets/fonts/Montserrat-SemiBold.ttf');
}


body {
  --black:            #000000;
  --dark-gray:        #323232;
  --gray:             #C9C9C9;
  --emerald-gradient: linear-gradient(91.83deg, #93E08C -1.93%, #78CEBF 97.03%);
  --light-green:      #93E08C;
  --accent-link:      #479C3F;

  --font-form-label:    12px;
  --font-section-text:  14px;
  --font-button-text:   15px;
  --font-subtitle:      16px;
  --font-section-title: 24px;
  --font-page-title:    32px;

  --bottom-indent: 24px;

  @media (max-width: 767px) {
    --bottom-indent: 12px;
  }
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;

  &:hover {
     text-decoration: underline;
   }
}



